import React, { PropTypes } from 'react';
import FacebookLogin from 'react-facebook-login';
import { Form1, Form2, Title4Top, Separator, Input, Button3, Link2, Total, SmallText, Link, Button2, Note, Row, Choice } from './styledComponents';
import CreditCard from './CreditCard';

const fixtures = {
  firstname: 'Benjamin',
  lastname: 'Michel',
  street: '11 rue de Poissy',
  zipcode: '75005',
  city: 'Paris',
  phone: '0612345678',
  email: 'benjamin@hello.com',
};

class InfosBlock extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  renderForm() {
    return (
      <Form1>
        <Title4Top>Mes coordonnées</Title4Top>
        <Row>
          <Input half type="text" placeholder="Votre nom" value={this.state.lastname} />
          <Input half type="text" placeholder="Votre prenom" value={this.state.firstname} />
        </Row>
        <Row>
          <Input half type="email" placeholder="Adresse email" value={this.state.email} />
          <Input half type="tel" placeholder="Téléphone" value={this.state.phone} />
        </Row>
        {this.state.newAccount && <Input type="password" placeholder="Votre mot de passe" />}
        {this.state.newAccount &&
          <Input type="password" placeholder="Confirmer votre mot de passe" />}

        <Title4Top>Mon adresse</Title4Top>
        <Input type="text" placeholder="Voie" value={this.state.street} />
        <Row>
          <Input half type="text" placeholder="Code postal" value={this.state.zipcode} />
          <Input half type="text" placeholder="Ville" value={this.state.city} />
        </Row>
        {/* <Link2 href="#">Ajouter une nouvelle adresse</Link2> */}
        <Title4Top>Mon moyen de paiement</Title4Top>
        {this.state.newAccount ?
          <CreditCard />
          : <div>
            {this.state.showCreditCard ?
              <CreditCard />
              : <div>
                <Choice isSelected={!this.state.showCreditCard}>4XXX-XXXX-XXXX-1234 - 01/18</Choice>
                <Choice onClick={() => this.setState({ showCreditCard: true })}>
                  Ajouter une nouvelle carte bancaire
                </Choice>
              </div>
            }
          </div>
        }
        <Separator />
        <Title4Top>Récapitulatif</Title4Top>
        <Total>Rdv le 27/03 entre 17h et 17h30</Total>
        <Total>
          Montant total : {this.props.total}€;
        </Total>
        <Link to={'/order/step2'}>
          <Button2>
            Valider mon RDV
          </Button2>
        </Link>
        <SmallText>
          En validant, vous acceptez les&nbsp;
          <Link href="#">
            conditions générales
          </Link>
          &nbsp;de ventes de tilli
        </SmallText>
        <Note>
          {`Lors du rdv 1 avec votre tilliste, vous pourrez réajuster les vêtements/retouches commandés.
          Attention si vous enlevez des vêtements/retouches, un minimum de 20€ de commande
          s'applique tout de même.`}
        </Note>
      </Form1>
    );
  }

  renderLogin() {
    return (
      <Form2>
        <Separator />
        <FacebookLogin
          appId="258420877937846"
          autoLoad
          fields="name,email,picture"
          onClick={(...args) => console.log('ComponentClick', args)}
          callback={(...args) => console.log(args)}
          version="2.8"
          language="fr"
          textButton="Se connecter avec Facebook"
        />
        <Separator />
        <Title4Top>Se connecter avec son compte</Title4Top>
        <Input type="email" placeholder="Votre adresse email" />
        <Input type="password" placeholder="Votre mot de passe" />
        <Button3 onClick={() => this.setState({ loggedIn: true, ...fixtures })}>
          Se connecter
        </Button3>
        <Separator />
        <Title4Top>Pas encore de compte ?</Title4Top>
        <Button3 onClick={() => this.setState({ loggedIn: true, newAccount: true })}>{"S'inscrire"}</Button3>
        <Separator />
      </Form2>
    );
  }

  render() {
    if (this.state.loggedIn) {
      return this.renderForm();
    }
    return this.renderLogin();
  }
}

InfosBlock.propTypes = {
  total: PropTypes.string,
};

export default InfosBlock;



