import React, { Component } from 'react';
import '../../App.css';
import InfosBlock from '../../components/InfosBlock';

import { Wrapper, Block, Title2 } from '../../components/styledComponents';

class Step5 extends Component {
  render() {
    return (
      <Wrapper>
        <Block id="infos">
          <Title2>
            Mes informations
          </Title2>
          <InfosBlock total={this.state.total.toFixed(2)} />
        </Block>
      </Wrapper>
    );
  }
}

export default Step5;



